import React from "react";
import { GAevent } from "../../../../utils/googleAnalyticsHelper";
import styles from "./QRAR.module.scss";

const QRAR = ({ HasAR, product }) => {
  if (!HasAR) return null;
  return (
    <div>
      <p className={styles.arText}>Scan/Click for AR</p>
      <a
        href={product.QR}
        target="blank"
        onClick={() => GAevent("Guest-User", "click", "QR Code Button")}
      >
        <img src={process.env.PUBLIC_URL + "/images/qr-" + product.Model + ".png"} className={styles.qrCode} alt="QR Code" />
      </a>
    </div>
  );
};

export { QRAR };
